footer {
  background: url('../img/4.jpg') no-repeat;
  background-position: center center;
  background-size: cover;
  @media(min-width: 796px) {
    background-attachment: fixed; }
  .container-outer {
    padding: 1.2rem 0;
    background: rgba(0,0,0,0.75);
    backdrop-filter: blur(3px);
    .col {
      min-height: 50px;
      #copy {
        font-size: 1.5rem;
        margin-bottom: 1rem; } }
    .col-12 {
      a {
        button.btn-success {
          background: linear-gradient(-8deg, lighten($forthcol, 25%) 0%, $forthcol 100%);
          border: 0;
          font: 1.3rem $mainFont !important;
          letter-spacing: 0.2em;
          padding: .3em .6em;
          text-shadow: -1px 1px 2px rgba(black, .7);
          transition: all ease 300ms;
          &:hover {
            transform: translateY(3px);
            box-shadow: 0 8px 4px -6px rgba(black, .6);
            span {
              animation-name: tada;
              animation-duration: 1s; } }
          span {
            float: left;
            padding-top: .13em;
            padding-right: .3em; }
          &:focus {
            outline: none !important; } } } }
    #social {
      a {
        i {
          font-size: 2.5rem;
          color: darken($secondcol, 8%) !important;
          text-shadow: 0 2px 2px rgba(black, .5);
          &:hover {
            color:  complement(darken($secondcol, 8%)) !important;
            animation: tada 1s; } } } } } }
.modal.fade {
  background: rgba(0, 0, 0, 0.7);
  .modal-dialog {
    max-width: 60vw;
    margin: 2rem auto;
    @media (max-width: 975px) {
      max-width: 80vw; }
    transition: all cubic-bezier(.21, -2, .51, 3.4) 300ms;
    .modal-content {
      background: linear-gradient(-185deg, lighten($forthcol, 20%) 0%, darken($forthcol, 14%) 100%);
      border: 0;
      .modal-header {
        border: 0;
        button {
          font-size: 4rem;
          position: absolute;
          top: 0px;
          right: 30px;
          transition: all ease 300ms;
          &:hover {
            transform: scale(1.4); }
          &:active, &:focus {
            outline: none; } }
        h4 {
          letter-spacing: 0.25em;
          margin-bottom: 0px;
          font: 2rem $mainFont;
          i {
            float: left;
            padding-right: 5px;
            margin: 5px; } } }
      .modal-body {
        a {
          font-size: 2rem;
          letter-spacing: 0.2em;
          border-radius: .15em;
          text-shadow: -1px 1px 1px #000;
          transition: all ease 300ms;
          &:hover {
            transform: translateY(3px);
            box-shadow: 0 8px 6px -8px #000; }
          i.fab, i.far {
            float: left;
            line-height: 2rem;
            margin-left: 30px;
            margin: 10px; }
          &:hover {
            i.fab, i.far {
              animation-name: tada;
              animation-duration: 1s; } } } }
      .modal-footer {
        border: 0;
        button {
          font-size: 1.3rem;
          padding: .3em .6em;
          background: linear-gradient(-8deg, lighten($forthcol, 25%) 0%, $forthcol 100%);
          color: #fff;
          letter-spacing: 0.14em;
          transition: all ease 300ms;
          &:hover {
            transform: translateY(3px);
            box-shadow: 0 5px 6px -4px #000; } } } } } }
