.cards {
	padding: 1rem;
	.cards-inner {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: 100%;
		width: 100%;
		background: linear-gradient(-8deg, lighten($forthcol, 25%) 0%, $forthcol 100%);
		&:hover {
			span {
				animation-name: wobble;
				animation-duration: 1s; } } }
	span {
		font-size: 2rem;
		color: #fff;
		padding: 1rem 0;
		text-shadow: 0 2px 2px #000;
		@media(min-width: 975px) {
			font-size: 4rem;
			padding: 2rem 0; } }
	p {
		color: #fff;
		font-size: 1.2rem;
		text-shadow: 0 2px 2px #000;
		@media(min-width: 975px) {
			font-size: 2rem; } } }
#about {
	a {
		i {
			color: #222;
			transition: all ease 300ms;
			&:hover {
				scale: 1.1; } } } }
#skills {
	i.fas {
		margin: -40px 0 10px 15px; }
	p {
		font-family: $mainFont;
		font-size: 2rem;
		font-weight: 400;
		margin-left: 60px;
		margin-bottom: -30px; }
	i.fab {
		&:hover {
			animation-name: tada;
			animation-duration: 1s; } } }
