#cookies {
  opacity: 0.97;
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 1rem 2rem;
  width: 100%;
  background: linear-gradient(-8deg, lighten(#903487, 20%) 0%, saturate(darken(#903487, 10%), 10%) 100%);
  transition: all ease 300ms;
  z-index: 99;
  a {
    color: #fff; }
  .btn {
    float: right;
    background: linear-gradient(-8deg, lighten(#3e6d81, 25%) 0%, #3e6d81 100%);
    padding: 1rem 2rem;
    box-shadow: 0 20px 6px -15px #000;
    transition: all ease-in-out 300ms;
    border: 0;
    &:hover {
      transform: translateY(3px);
      box-shadow: 0 10px 6px -7px #000; } } }
